import React, { useEffect } from 'react'

const KakaoShareButton = () => {
    useEffect(() => {
        createKakaoButton()
    }, [])

    const createKakaoButton = () => {
        if (window.Kakao) {
            const kakao = window.Kakao

            if (!kakao.isInitialized()) {
                kakao.init("4f324ea3b2c74882913007ae064bb5ef")
            }

            kakao.Link.createDefaultButton({
                container: '#kakao-link-btn',
                objectType: 'feed',
                content: {
                    title: '  !',
                    description: '2024 4 13()  3  ',
                    imageUrl: "https://raw.githubusercontent.com/hojunyeun/hojunyeun.github.io/main/img/share.jpg",
                    link: {
                        mobileWebUrl: window.location.href,
                        webUrl: window.location.href,
                    },
                },
                buttons: [
                    {
                        title: ' ',
                        link: {
                            mobileWebUrl: window.location.href,
                            webUrl: window.location.href,
                        },
                    }
                ],
            })
        }
    }

    return (
        <div className="KakaoShare" id="kakao-link-btn">
            <text className="KakaoShareText"> </text>
            <button className="KakaoShareButton">
                <img className="KakaoShareIcon" src={"/img/kakaoshare_button.png"} alt="kakao-share-icon" />
            </button>
        </div>
    )
}

export default KakaoShareButton